import { samplesDict } from './dictionary';
export default function tokenize(regex) {
    let backslash = false, currentBracket = null;
    const str = regex.toString().slice(1, -1), samples = [], push = (s) => (currentBracket || samples).push(s);
    for (const char of str) {
        switch (char) {
            case '\\':
                backslash = true;
                continue;
            case '[':
                currentBracket = [];
                continue;
            case ']':
                samples.push(currentBracket.flat());
                currentBracket = null;
                continue;
        }
        if (backslash) {
            if (char in samplesDict)
                push(samplesDict[char]);
            else
                push(eval(`"\\${char}"`));
        }
        else if (char === '.')
            push(samplesDict['.']);
        else
            push([char]);
        backslash = false;
    }
    return samples;
}
